*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body{
    margin: 0;
    padding: 0;
}

.editor {
    display: flex;
    justify-content: space-around;
    gap: 30px;
}

#editor {   
    width: 500px;
    height: 300px;
    margin: 30px auto;
    padding: 20px;
}

#preview {
    width: 500px;
    height: 300px;
    margin: 30px auto;
    padding: 20px;
    background-color: aquamarine;
}